.footer {
  font-size: 1.6rem;
  background-color: var(--footerBg);

  .footerContainer {
    min-height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    a {
      height: 30px;
      width: 30px;
      text-decoration: none;
      font-size: 1.6rem;
      color: white;
      text-align: center;

      line-height: 25px;
      background-color: #d4ac2b;
      border-radius: 50%;

      &:hover {
        background-color: #b05e27;
        border-radius: 50%;
      }
    }

    img {
      width: 40px;
    }
  }
}
